.bggradient {
  background: #46417d;
  /* Old browsers */
  background: -moz-linear-gradient(top,
      #2e305c 0%,
      #46417d 100%);
  /* FF3.6-15 */
  background: -webkit-linear-gradient(top,
      #2e305c 0%,
      #46417d 100%);
  /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(to bottom,
      #2e305c 0%,
      #46417d 100%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
}

.bluegradient {
  background: #26d5f9;
  /* Old browsers */
  //   background: -moz-linear-gradient(top,  #4efff8 0%, #76baff 100%); /* FF3.6-15 */
  //   background: -webkit-linear-gradient(top,  #4efff8 0%,#76baff 100%); /* Chrome10-25,Safari5.1-6 */
  //   background: linear-gradient(to bottom,  #4efff8 0%,#76baff 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  color: #fff;
}

.pinkgradient {
  background: #cf43ca;
  /* Old browsers */
  //   background: -moz-linear-gradient(top,  #ff989b 0%, #ff5177 100%); /* FF3.6-15 */
  //   background: -webkit-linear-gradient(top,  #ff989b 0%,#ff5177 100%); /* Chrome10-25,Safari5.1-6 */
  //   background: linear-gradient(to bottom,  #ff989b 0%,#ff5177 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  color: #fff;
}

.darkbluegradient {
  background: #6983fb;
  /* Old browsers */
  //   background: -moz-linear-gradient(top,  #9dacf2 0%, #6384e9 100%); /* FF3.6-15 */
  //   background: -webkit-linear-gradient(top,  #9dacf2 0%,#6384e9 100%); /* Chrome10-25,Safari5.1-6 */
  //   background: linear-gradient(to bottom,  #9dacf2 0%,#6384e9 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  color: #fff;
}

.orangegradient {
  background: #f3bc00;
  /* Old browsers */
  //   background: -moz-linear-gradient(top,  #ffb26a 0%, #ff722d 100%); /* FF3.6-15 */
  //   background: -webkit-linear-gradient(top,  #ffb26a 0%,#ff722d 100%); /* Chrome10-25,Safari5.1-6 */
  //   background: linear-gradient(to bottom,  #ffb26a 0%,#ff722d 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  color: #fff;
}

.greengradient {
  background: #5CCB9A;
  /* Old browsers */

  color: #fff;
  position: relative;

  &:after {
    content: '';
    position: absolute;
    width: 159px;
    height: 34px;
    left: 0;
    right: 0;
    margin: auto;
    bottom: 7px;
    background-color: #E2E2E2;
    z-index: -1;
    border-radius: 10px;
  }
}

.btngradient {
  color: #fff;
  background: #a2f023;
  /* Old browsers */
  border: 0;
  height: auto;
  text-shadow: 0 1px 0 rgba(255, 255, 255, 0.4);
  -webkit-box-shadow: 0px 4px 0px #caff68;
  -moz-box-shadow: 0px 4px 0px #caff68;
  box-shadow: 0px 4px 0px #519105;
}

::-webkit-scrollbar {
  width: 0px;
  /* Remove scrollbar space */
  background: transparent;
  /* Optional: just make scrollbar invisible */
}

/* Optional: show position indicator in red */
::-webkit-scrollbar-thumb {
  background: transparent;
}

*:focus {
  outline: none;
}

.food,
.music,
.movies,
.sport,
.animals,
.cars,
.general,
.health {
  position: relative;

  &:before {
    content: "";
    position: absolute;
    height: 20px;
    width: 20px;
    left: 0;
    right: 0;
    top: 7px;
    margin: auto;
    background-size: cover;
    background-repeat: no-repeat;
  }
}

.sport {
  &:before {
    background-image: url("../assets/coin.svg");
  }
}

.movies {
  &:before {
    background-image: url("../assets/filmstrip-icon.svg");
  }
}

.music {
  &:before {
    background-image: url("../assets/music-icon.svg");
  }
}

.food {
  &:before {
    background-image: url("../assets/food-icon.svg");
  }
}

.animals {
  &:before {
    background-image: url("../assets/paw-icon.svg");
  }
}

.cars {
  &:before {
    // background-image: url('../assets/wheel-icon.svg');
  }
}

.general {
  &:before {
    background-image: url("../assets/brain.svg");
  }
}

.health {
  &:before {
    background-image: url("../assets/medical-bag.svg");
    height: 15px;
    width: 15px;
  }
}

a {
  color: inherit;
  text-decoration: none;
}

body {
  position: relative;
  overflow: hidden;

  background: rgb(0, 0, 0);
  background: linear-gradient(to bottom, black 5%, #1c0e58 39%, #291176 66%, #35138c 81%);

  color: #ffb124;
  height: 100vh;
  width: 100vw;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    opacity: 0.6;
    left: 0;
    height: 50vh;
    width: 100vw;
    z-index: 999;
    z-index: -1;
    background-image: url("../assets/stars.svg");
    background-repeat: repeat;
    background-position: top left;
    background-size: contain;
  }

}

