}

.profile {
  padding: 15px;
  max-width: 100vw;
  overflow: hidden;

  &__top {
    display: flex;
    justify-content: space-between;

    .profile__user {
      background-size: contain;
      // display: flex;
      background-color: #31b03e;
      color: #fff;
      border-radius: 4px;
      margin-right: 8px;
      margin-bottom: 8px;
      position: relative;

      .user__img {
        height: 30px;
        width: 30px;
        margin-left: 10px;
        position: absolute;
        top: -4px;
        left: -4px;
        border-radius: 4px;
        border: 2px solid gold;
      }

      .profile-name {
        // background-image: url('../assets//btn-bg.svg');

        background-repeat: no-repeat;
        background-size: cover;
        font-size: 12px;
        padding: 7px 14px;
        padding-left: 45px;
        align-self: center;
        display: block;
        position: relative;
      }
    }

    .profile__coins {
      align-self: flex-start;
      border-radius: 4px;
      padding: 6px 10px;
      padding-left: 40px;
      background-color: #feb144;
      color: #fff;
      position: relative;
      font-size: 14px;

      &::after {
        content: "";
        position: absolute;
        height: 22px;
        width: 22px;
        left: 6px;
        top: 2px;
        background-image: url("../assets/coin.svg");
      }
    }
  }

  &__bottom {
    display: flex;

    .profile__game-chest {
      flex: 1.4;
      padding: 6px 10px;
      padding-left: 40px;
      background-color: #3a97be;
      color: #fff;
      margin-right: 8px;
      border-radius: 4px;
      position: relative;
      font-size: 14px;
      font-weight: bold;
      border: 0;

      &::after {
        content: "";
        position: absolute;
        height: 28px;
        width: 36px;
        left: 2px;
        top: -2px;
        background-image: url("../assets/trophie.svg");
        background-repeat: no-repeat;
      }
    }

    .profile__free-chest {
      flex: 1.4;
      padding: 6px 10px;
      padding-left: 40px;
      background-color: #e94033;
      color: #fff;
      margin-right: 8px;
      border-radius: 4px;
      position: relative;
      font-size: 14px;
      font-weight: bold;
      border: 0;

      &::after {
        content: "";
        position: absolute;
        height: 32px;
        width: 32px;
        left: 6px;
        top: -4px;
        background-image: url("../assets/chest.svg");
        background-repeat: no-repeat;
      }
    }

    .profile__germs {
      flex: 1;
      max-width: 140px;
      padding: 6px 10px;
      padding-left: 40px;
      background-color: #b57ecb;
      color: #fff;
      border-radius: 4px;
      font-size: 14px;
      position: relative;

      &::after {
        content: "";
        position: absolute;
        height: 25px;
        width: 25px;
        left: 6px;
        top: 1px;
        background-image: url("../assets/germ.svg");
        background-repeat: no-repeat;
      }
    }
  }
}

