.modal {

  background-color: #fff;
  border-top: 2px solid #5a6882;
  left: 0;
  right: 0;
  width: 95%;
  top: 10vh;
  max-height: 80vh;
  max-width: 100%;
  margin: auto;
  box-shadow: 0 7px 15px -1px rgba(0, 0, 0, .3);

  position: absolute;

  border-radius: 20px !important;
  animation-duration: 0.75s;
  animation-name: bounceIn;
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0;
  animation: example 200ms linear;

  &::before {
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    bottom: -3px;
    background-color: #E2E2E2;
    z-index: -9;
    border-radius: 20px !important;
  }
}

.modal-body {

  margin-top: -2px;
  padding-bottom: 10px;
  min-height: 200px;
}

.result-container .modal-body {
  background-image: url("../assets/stars.svg");
}

.modal-buttons {
  margin: 1em 0;
  padding-top: 1em;
}

