  border-radius: 8px;
  display: none;
}

// CHEST COMPONENETS

.chest-container {
  display: flex;
  margin-top: 20px;
  margin: 18vh 5px 0 5px;

  .chest {
    flex: 1;
    border-radius: 6px;
    color: #fff;
    margin: 10px 4px;
    padding: 8px 4px 0px 90px;
    position: relative;
    text-transform: uppercase;
    min-height: 75px;

    &--free {
      background: #45e534;
      /* Old browsers */

      position: relative;

      &::before {
        position: absolute;
        content: "";
        height: 45px;
        width: 45px;
        background-size: cover;
        left: 0;
        top: -8px;
        background-image: url("../assets/graduation.svg");
        background-repeat: no-repeat;
      }
    }

    &--game {
      background-color: #0b74b0;
      position: relative;

      &::before {
        position: absolute;
        content: "";
        height: 45px;
        width: 45px;
        background-size: cover;
        left: 0;
        top: -8px;
        background-image: url("../assets/diploma.svg");
        background-repeat: no-repeat;
      }
    }

    &__loader-wrap {
      width: 100%;
      right: 0;
      left: 0;
      bottom: 0px;
      background-color: rgba(0, 0, 0, 0.14);
      padding: 7px 0;
      border-bottom-left-radius: 5px;
      border-bottom-right-radius: 5px;

      margin: auto;
      position: absolute;

      span {
        text-align: center;
        display: block;
      }

      .loading-bar {
        width: 60%;
        height: 10px;
        /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#fceabb+0,fccd4d+50,f8b500+51,fbdf93+100;Orange+3D+%235 */
        background: #fceabb;
        /* Old browsers */
        background: -moz-linear-gradient(top,
            #fceabb 0%,
            #fccd4d 50%,
            #f8b500 51%,
            #fbdf93 100%);
        /* FF3.6-15 */
        background: -webkit-linear-gradient(top,
            #fceabb 0%,
            #fccd4d 50%,
            #f8b500 51%,
            #fbdf93 100%);
        /* Chrome10-25,Safari5.1-6 */
        background: linear-gradient(to bottom,
            #fceabb 0%,
            #fccd4d 50%,
            #f8b500 51%,
            #fbdf93 100%);
        /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */

        margin-left: 0px;
        border-bottom-left-radius: 3px;
        border-top-left-radius: 3px;
        width: 85%;
        margin-bottom: 3px;
      }

      .loading-bg {
        background: #000;

        margin: auto;
        height: 10px;
        width: 85%;
        border-radius: 3px;

        span {
          position: relative;
          z-index: 9;
          position: absolute;
          font-size: 11px;
          bottom: 10px;
        }
      }
    }

    &__status {
      margin-left: 4px;

      h3 {
        margin: -5px;
        font-size: 1.1em;
        font-weight: bold !important;
        width: 100px;
      }

      p {
        font-size: 12px;
        margin: 3px 0;
        display: block;
      }
    }
  }
}

// QUESTION

.questions {
  position: absolute;
  top: 0;
  padding-top: 10vh;
  background-color: rgba(0, 0, 0, 0.8);
  width: 100%;
  left: 0;
  height: 100vh;
  overflow: scroll;
}

.question-wrapper {
  max-width: 700px;
  margin: auto;
  color: #777777;
  margin: auto;
  padding: 1em;
}

.question {
  background-color: #fff;
  border-radius: 10px;
  padding: 1em 0 2.5em 0;
  position: relative;

  .user {
    &__img {
      width: 60px;
      height: 60px;
    }
  }

  &__image {
    // margin-top:70px;
    max-height: 130px;
    width: 100%;
  }

  &__count-down {
    background-color: #fff;
    height: 40px;
    width: 40px;
    margin: auto;
    margin-top: -20px;
    margin-bottom: 10px;
    line-height: 40px;
    font-size: 17px;
    border-radius: 50%;
    border: 1px solid #ccc;
    text-align: center;
    color: #f3bc00;
  }

  &__counter {
    text-align: center;
    font-size: 11px;
    font-weight: $light-font;
    margin-bottom: 10px;
  }

  &__title {
    text-align: center;
    font-size: 16px;
    margin: 0 20px;
  }

  &__options {
    display: flex;
    flex-wrap: wrap;
    padding: 0 10px;
    max-height: 50vh;
    overflow: scroll;
    max-width: 700px;
    margin: auto;
    counter-reset: section;

    .option {

      font-size: 15px;
      line-height: 22px;
      font-weight: bold;
      height: auto;
      white-space: normal;
      border-radius: 7px;
      position: relative;
      width: 100%;
      padding: 10px 10px;
      text-align: left;
      border: 0;
      padding-left: 48px;
      background: rgb(255, 255, 255);
      /* Old browsers */
      background: -moz-linear-gradient(top, rgba(255, 255, 255, 1) 62%, rgba(239, 239, 239, 1) 83%);
      /* FF3.6-15 */
      background: -webkit-linear-gradient(top, rgba(255, 255, 255, 1) 62%, rgba(239, 239, 239, 1) 83%);
      /* Chrome10-25,Safari5.1-6 */
      background: linear-gradient(to bottom, rgba(255, 255, 255, 1) 62%, rgba(239, 239, 239, 1) 83%);


      &:after {
        counter-increment: section;
        content: counter(section);
        position: absolute;
        left: 8px;
        background-color: #fff;
        border: 1px solid rgba(204, 204, 204, 0.27);
        color: #5a5959;
        height: 25px;
        width: 25px;
        border-radius: 50%;
        text-align: center;
        line-height: 27px;
        top: 7px;
      }
    }
  }
}

// OPTION ANSWER

.result-container {
  background-color: rgba(0, 0, 0, 0.83);
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: 99999;
  height: 100vh;
  -webkit-animation-duration: 0.75s;
  animation-duration: 0.75s;
  -webkit-animation-name: bounceIn;
  animation-name: bounceIn;
}

.result {
  padding: 4em 1.5em 0 1.5em;

  .profile__user {
