.banner {
  height: 130px;
  width: 95%;
  display: flex;
  margin: auto;
  margin-bottom: 2em;
  padding-top: 50px;
  position: absolute;
  bottom: 40vh;
  left: 0;
  right: 0;

  .pedal {
    position: relative;
    border-top-left-radius: 15px;
    z-index: 9;

    &:last-child {
      border-top-right-radius: 15px;
    }

    &__header {
      background: rgb(255, 255, 255);
      /* Old browsers */
      background: -moz-linear-gradient(top,
          rgba(255, 255, 255, 1) 6%,
          rgba(226, 226, 226, 1) 27%,
          rgba(255, 255, 255, 1) 98%);
      /* FF3.6-15 */
      background: -webkit-linear-gradient(top,
          rgba(255, 255, 255, 1) 6%,
          rgba(226, 226, 226, 1) 27%,
          rgba(255, 255, 255, 1) 98%);
      /* Chrome10-25,Safari5.1-6 */
      background: linear-gradient(to bottom,
          rgba(255, 255, 255, 1) 6%,
          rgba(226, 226, 226, 1) 27%,
          rgba(255, 255, 255, 1) 98%);
      /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */

      padding-top: 15px;
      border-top-left-radius: 15px;
      border-top-right-radius: 15px;
      color: #000;
    }

    flex: 1;
    text-align: center;

    &:nth-child(2) {
      // border-top: 2px solid #5a6882;
      border-top-left-radius: 15px;
      border-top-right-radius: 15px;

      margin-top: -15px;
    }

    &:nth-child(1) {
      border-top-right-radius: 0px;
    }
  }
}

