body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Page */

.page {
  height: 100vh;
  padding: calc(15% + 1.5em) 5% 5%;
  overflow-y: auto;
  position: fixed;
  top: 0;
  width: 100%;
  -webkit-overflow-scrolling: touch;
  transition: transform 1s ease-in-out, box-shadow 1s ease-in-out;
}

.page-enter {
  transform: translate(-100%, 0);
}

.page-enter-active {
  transform: translate(0, 0);
}

.page-exit {
  box-shadow: 0 0 5em 0 rgba(0, 0, 0, .5) inset;
  transform: translate(-100%, 0);
}

.page--prev.page-enter {
  transform: translate(100%, 0);
}

.page--prev.page-enter-active {
  transform: translate(0, 0);
}

.page--prev.page-exit {
  transform: translate(100%, 0);
}

.page-exit .page__inner {
  opacity: 0;
  transform: scale(0.9);
  transition: transform 1s ease-in-out, opacity 1s ease-in-out;
}

.option.correct {

  background: rgb(255, 255, 255);
  /* Old browsers */
  background: -moz-linear-gradient(top, #BEFFE2 62%, #96f1c9 83%);
  /* FF3.6-15 */
  background: -webkit-linear-gradient(top, #BEFFE2 62%, #96f1c9 83%);
  /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(to bottom, #BEFFE2 62%, #96f1c9 83%);
  color: #fff;

  &::before {
    content: 'Ohh yeh!';
    position: fixed;
    top: 120px;
    padding: 16px 1em;
    width: 200px;
    background-color: rgba(40, 171, 113, 0.78);
    text-align: center;
    font-size: 22px;
    border-radius: 22px;
  }

}

.option.wrong {
  background: rgb(255, 255, 255);
  /* Old browsers */
  background: -moz-linear-gradient(top, #FFB1B1 62%, #E37979 83%);
  /* FF3.6-15 */
  background: -webkit-linear-gradient(top, #FFB1B1 62%, #E37979 83%);
  /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(to bottom, #FFB1B1 62%, #E37979 83%);
  color: #fff;
  color: #fff !important;

  &::before {
    content: 'Ohh no!';
    position: fixed;
    top: 120px;
    padding: 16px 1em;
    width: 200px;
    background-color: rgba(181, 9, 59, 0.83);
    text-align: center;
    font-size: 22px;
    border-radius: 22px;
  }
}

.page h1 {
  margin-top: 0;
}

.err-modal {
  position: absolute;
  top: 0;
  width: 100%;
  background-color: #e74c3c;
  color: #fff;
  z-index: 99999;
  padding: 10px;
  text-align: center;
  font-size: 16px;
}

/* starting ENTER animation */
.messageout-enter {
  position: absolute;
  top: 0;
  left: calc(13% + 5px);
  right: calc(13% + 5px);
  opacity: 0.01;
  transform: translateY(0%) scale(0.01);
}

/* ending ENTER animation */
.messageout-enter-active {
  opacity: 1;
  transform: translateY(0%) scale(1);
  transition: all 1000ms ease-in-out;
}

/* starting EXIT animation */
.messageout-exit {
  opacity: 1;
  transform: scale(1.01);
}

/* ending EXIT animation */
.messageout-exit-active {
  opacity: 0;
  // transform: scale(4);
  transition: all 1000ms ease-in-out;
}

.fade-appear {
  opacity: 0;
  z-index: 1;
}

.fade-appear.fade-appear-active {
  opacity: 1;
  transition: opacity 300ms linear;
}

.fade-enter {
  position: absolute;

  left: 0;
  right: 0;
  margin: auto;
  opacity: 0;
  z-index: 1;
  transform: scale(0.50) translateY(5px);
}

.fade-enter.fade-enter-active {
  opacity: 1;
  transform: scale(1) translateY(0);
  transition: opacity 300ms linear 300ms, transform 300ms ease-in-out 300ms;
}

.fade-exit {
  opacity: 1;
  transform: scale(1) translateY(0);
}

.fade-exit.fade-exit-active {
  opacity: 0;
  transform: scale(0.50) translateY(5px);
  transition: opacity 300ms linear, transform 300ms ease-out;
}

.fade-exit-done {
  opacity: 0;
}